@import "src/core/components/App/App";

.header-app {
  position: fixed !important;
  top: 0 !important;
  width: 100% !important;
  z-index: 2 !important;
  padding: 0 20px 0 20px;
  background-color: $background-color !important;
  color: $frontend-color!important;

  .title {
    color: rgba(0, 0, 0, .55);
    white-space: normal;
  }
}

@media (max-width: 767px) {
  .header-app {
    display: none!important;
  }
}
