@import "src/core/components/App/App";

.bottom-app {
  position: fixed !important;
  bottom: 0 !important;
  width: 100% !important;
  z-index: 2 !important;
  background-color: $background-color !important;
  color: $frontend-color !important;
  display: flex;
  justify-content: space-around;

  &-item {
    width: 100%;
    height: 3.5rem;
  }

  &-icon {
    font-size: 1.5rem !important;

  }

}

@media (min-width: 768px) {
  .bottom-app {
    display: none;
  }
}
